import React from "react"
import { MDBCard, MDBCardBody, MDBCardText, MDBCardHeader, MDBIcon, MDBBox } from "mdbreact"

const Details = () => (
  <>
    <section className="details">
      <MDBCard className="h-100">
        <MDBCardHeader color="light-green lighten-3" className="custom-dark-text">
          Программа проведения олимпиады
        </MDBCardHeader>
        <MDBCardBody>
          <div className="d-flex">
            <MDBIcon icon="map-marker-alt" className="deep-orange-text card-fa-icon mr-2" />
            <MDBCardText>Олимпиада будет проведена дистанционно (в режиме он - лайн) на интернет &mdash; портале &laquo;ЯКласс&raquo;</MDBCardText>
          </div>
          <div className="d-flex my-2">
            <MDBIcon far icon="calendar-alt" className="deep-orange-text card-fa-icon mr-2" />
            <MDBCardText>&laquo;28&raquo; марта 2020 года</MDBCardText>
          </div>
          <div className="d-flex">
            <MDBIcon icon="clock" className="deep-orange-text card-fa-icon mr-2" />
            <MDBCardText>в период с 11.00 до 12.00</MDBCardText>
          </div>
        </MDBCardBody>
      </MDBCard>
      <MDBBox tag="p" my={5} className="text-center">
        <span className="d-block">По всем дополнительным вопросам участия в Олимпиаде просим обращаться на адрес электронной почты</span>
        <a href="mailto:info@yaklass.by" className="mail-link">info@yaklass.by</a>
      </MDBBox>
    </section>
  </>
)

export default Details
