import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { MDBContainer, MDBIcon, MDBRow, MDBCol } from "mdbreact"

const Prizes = () => {
  const data = useStaticQuery(graphql`
    query {
      prize: file(relativePath: { eq: "smartphone-w250.png" }) {
        childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <>
      <section className="prizes mt-5 z-depth-1">
        <MDBContainer size="lg" className="white-text prizes__text-block">
          <MDBRow>
            <MDBCol md="6" className="py-5 mx-auto text-center">
              <Img fixed={data.prize.childImageSharp.fixed} />
            </MDBCol>
            <MDBCol md="6" className="py-5 d-flex flex-column">
              <div className="d-flex flex-grow-1 row-icon-txt">
                <MDBIcon icon="trophy" className="orange-text" />
                <p className="lead">Победитель Олимпиады, занявший первое место, награждается смартфоном!</p>
              </div>
              <div className="d-flex flex-grow-1 row-icon-txt">
                <MDBIcon icon="award" className="yellow-text" />
                <p className="lead">Участники, занявшие 2 и 3 места – ценными призами!</p>
              </div>
              <div className="d-flex flex-grow-1 row-icon-txt">
                <MDBIcon icon="graduation-cap" className="deep-purple-text" />
                <p className="lead">Все участники Олимпиады получают почетные дипломы!</p>
              </div>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </section>
    </>
  )
}

export default Prizes
