import React, { useState } from 'react';
import { MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter, MDBIcon } from 'mdbreact';

const PayBtn = ({ func }) => {

  return (
    <MDBBtn onClick={func}>
      Оплатить <MDBIcon far icon="credit-card" />
    </MDBBtn>
  )
}

export default PayBtn;