import React from 'react';
import Modal from "../Modal/modal";
import PayBtn from "../PayBtn/payBtn";
import { MDBJumbotron, MDBCardBody, MDBCardText, MDBCardTitle, MDBInput } from "mdbreact";

const Jumbotron = () => {
  const box = React.createRef();

  const onPay = () => {
    const isCheck = box.current.inputElementRef.current.checked;
    console.log('isCheck', isCheck);
    // if (isCheck) {
    //   console.log('location', window.location);
    //   window.location = 'https://checkout.bepaid.by/v2/confirm_order/prd_fbb3a61f35993b1f/9435';
    // }
  }

  return (
    <>
      <section>
        <MDBJumbotron className="text-center mx-auto p-3 my-5">
          <MDBCardBody>
            <MDBCardTitle>Регистрации и&nbsp;оплата участия</MDBCardTitle>
            <p className="default-color-text my-4 font-weight-bold">Участие в&nbsp;Олимпиаде&nbsp;&mdash; добровольное и&nbsp;платное</p>
            <p>Стоимость участия в Олимпиаде - 3 бел. рубля</p>
            <MDBCardText>
              Для участия в&nbsp;Олимпиаде необходимо ознакомится с у&nbsp;Инструкцией
          </MDBCardText>
            <hr className="my-4" />
            <div className="pt-2">
              <Modal />
              <PayBtn func={onPay} />
            </div>
            <div className="custom-input-checkbox">
              <MDBInput label="С условиями проведения Олимпиады согласен" filled type="checkbox" id="checkbox2" ref={box} />
            </div>
          </MDBCardBody>
        </MDBJumbotron>
      </section>
    </>
  )
}

export default Jumbotron