import React, { useState } from 'react';
import { MDBBtn, MDBModal, MDBModalBody, MDBModalHeader, MDBModalFooter, MDBIcon, MDBInput } from 'mdbreact';

const Modal = () => {
  const [isOpen, toggle] = useState(false);

  const onModal = () => {
    toggle(!isOpen);
  }

  return (
    <>
      <MDBBtn onClick={onModal} outline>Инструкция <MDBIcon icon="info-circle" /></MDBBtn>
      <MDBModal isOpen={isOpen} toggle={onModal} className="custom-modal">
        <MDBModalHeader toggle={onModal} className="text-left">
          ИНСТРУКЦИЯ<br></br>
          О порядке участия в Межрегиональной Олимпиаде по математике<br></br>
          среди учащихся III – IV классов учреждений общего и среднего образования (далее- Олимпиада)
        </MDBModalHeader>
        <MDBModalBody>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi ut aliquip ex ea commodo consequat. Lorem ipsum dolor sit
              amet, consectetur adipisicing elit, sed do eiusmod tempor
              incididunt ut labore et dolore magna aliqua. Ut enim ad minim
              veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
        </MDBModalBody>
        <MDBModalFooter>
          <MDBBtn outline onClick={onModal}>Закрыть</MDBBtn>
        </MDBModalFooter>
      </MDBModal>
    </>
  );
}

export default Modal
