import React from "react"
import { MDBContainer } from "mdbreact";
import Intro from "./intro"
import Jumbotron from "./jumbotron"
import Details from "./details"
import Prizes from "./prizes"

const Main = () => (
  <>
    <MDBContainer size="lg">
      <Intro />
    </MDBContainer>
    <Prizes />
    <MDBContainer size="lg">
      <Jumbotron />
      <Details />
    </MDBContainer>
  </>
)

export default Main;